
import { mapState } from 'vuex'
import Qrcode from 'vux/src/components/qrcode/index.vue'

export default {
directives: {
},
components: {
Qrcode
},
name: 'myvcard',
data () {
return {
x_now: 0,
x_str: "",
timer: null,
show_qrcode: false
}
},
computed: {
...mapState({
openid: state => state.vux.openid,
}),
},
created: function() {

},
activated: function(){
console.log('hahahah')
this.setWxShare('全城洗车、随意分享。全家通用','全城汽车服务，都在你的身边','http://xcxcgj.gotomore.cn/index.html#/tmap','http://xcxcgj.gotomore.cn/public/xarlogo.jpg');
var self = this
self.fetchData()
self.timer = setInterval(function(){
var timestamp = Date.parse(new Date()) / 1000;
if(self.x_now + 30 < timestamp){
self.x_now = timestamp
self.fetchData()
}
},1000);
},
deactivated: function(){
clearInterval(this.timer)
},
methods: {
fetchData: function(){
let self = this
self.xarpost('AllQrcode/getMyVcard').then( d => {
let {data, error, message} = d
if(error == 0){
if(data){
self.x_str = data.x_str;
self.x_now = data.x_now;
self.show_qrcode = true
}else{
self.$vux.alert.show({
title: '小矮人',
content: '您还没有可以使用的洗车卡，现在带您去购买。',
onHide () {
self.$router.push('/vcard/2')
}
})
clearInterval(self.timer)
}
}else if(error == 10001){
clearInterval(self.timer)
self.showtoast(message)

}
})
}
}
}



